@import url(https://fonts.googleapis.com/css?family=Nunito:400,700|Open+Sans:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Nunito', sans-serif;
}

/* -- Body -- */

main {
  float: right;
  background: rgb(187, 245, 252);
  width: 100%;
  overflow: visible;
  height: calc(100vh - 60px);
}

section {
  width: 75%;
  font-size: 20px;
  color: #00072D;
  height: 100%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}


@media screen and (min-width: 600px) {
  .current {
    font-size: 23px;
  }
  main {
    float: none;
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  main {
    float: right;
    background: #a3e1ff;
    width: 80%;
    height: 100vh;
    overflow: visible;
  }
}
/* -- Header -- */

.current-link {
  font-weight: 700;
}

header {
  float: left;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  background: white;
  height: 60px;
}

header ul {
 list-style: none;
 padding: 0;
 display: flex;
 width: 100%;
 justify-content: center;
}

header ul a {
  font-size: 1rem;
  -webkit-transition: font-size .5s ease 0s, font-weight 0.3s ease 0s;
  transition: font-size .5s ease 0s, font-weight 0.3s ease 0s;
  padding: 0 12px;
}

header ul a {
  text-decoration: none;
  color: #000;
  position: relative;
}

a:hover:before {
  width: 100%;
  right: 0;
  -webkit-transition: width 0.3s ease 0s, right 0.3s ease 0s;
  transition: width 0.3s ease 0s, right 0.3s ease 0s;
}

a:before {
  bottom: 0;
  content: "";
  display: block;
  height: 1.5px;
  right: 0; 
  position: absolute;
  background: #2F74D0;
  width: 0;
  -webkit-transition: width 1s ease 0s, right 0.5s ease 0s;
  transition: width 1s ease 0s, right 0.5s ease 0s;
}

@media screen and (min-width:400px) {
  header ul a {
    font-size: 1.1rem;
  }
}

@media screen and (min-width: 600px) {
  header {
    float: none;
    width: 100%;
    display: flex;
    align-items: center;
    height: auto;
    background: white;
  } 
  header ul {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin: 0 auto;
  }
  header ul a {
    font-size: 20px; 
  }
}

@media screen and (min-width: 768px) {
    header {
      float: left;
      width: 20%;
      height: 100vh;
      display: flex;
      align-items: center;
      position: fixed;
    }
  
    header ul {
      flex-direction: column;
      margin: 0 auto;
    }
  
    header ul a {
      font-size: 1.6rem !important;
      padding: 20px;
      margin: 5px;
    }

    a:hover:before {
        width: 90%;
    }
}  
/* -- About -- */

.intro {
    background: rgb(187, 245, 252);
    width: 100%;
    font-size: 15px;
    line-height: 50px;
}

.intro h2 {
    padding: 20px;
}
  
@media screen and (min-width: 600px) {
    .about {
        line-height: 30px;
        font-size: 18px;
    }
}

@media screen and (min-width: 768px) {
    .intro {
        line-height: 42px;
        font-size: 20px;
        padding: 0 130px;
        margin: 0 auto;
        box-sizing: border-box;
    }
}

/* -- About -- */

.about {
    font-size: 15px;
    width: 100%;
    background: rgb(179, 255, 251);
}

.about p {
    padding: 25px;
}
  
@media screen and (min-width: 600px) {
    .about {
        line-height: 30px;
        font-size: 18px;
    }
}

@media screen and (min-width: 768px) {
    .about {
        line-height: 42px;
        font-size: 20px;
        padding: 0 100px;
        margin: 0 auto;
        box-sizing: border-box;
    }
}

/* -- Contact -- */

.contact { 
  text-align: center;
}

.contact-links svg {
  font-size: 40px;
  color: black;
  padding: 10px;
  -webkit-transition: font-size 0.5s ease 0s;
  transition: font-size 0.5s ease 0s;
}

.contact-links svg:active {
  position: relative;
  font-size: 60px;
  -webkit-transition: font-size 0.5s ease 0s;
  transition: font-size 0.5s ease 0s
}
/* -- Project -- */

.projects {
  display: flex;
  float: right;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background: black;
  justify-content: center;
}

.project-card {
  background: rgb(185, 204, 241);
  border: 1px solid rgb(185, 204, 241);
  border-radius: 2px;
  box-sizing: border-box;
  font-size: 18px;
  width: 80%;
  display: block;
  height: auto;
  border-radius: 0 0 3px 3px;
}

.project-card-block {
  padding-top: 5px;
  display: flex;
  justify-content: flex-start;
}

.project-card img {
  width: 100%;
  border-radius: 3px;
}

.project-card i {
  font-size: 30px;
}

.project-icons {
  display: flex;
  justify-content: flex-start;
}

.project-icons svg {
  padding: 0 5px;
  font-size: 1.7rem;
}

.project-description {
  padding: 20px;
}

.project-description h2 {
  margin: 0;
}

.project-description h2, .project-description p {
  font-family: 'Open Sans', sans-serif !important;
}

.project-card button {
  text-decoration: none;
  border-radius: 3px;
  margin-right: 10px;
}

.project-card button:hover {
  background: #98D2EB;
}

.project-card button a {
  text-decoration: none;
  color: inherit;
}

@media screen and (min-width:400px) {
  header ul a {
    font-size: 1.1rem;
  }

  .projects {
    overflow: scroll;
  }

  .project-card-list {
    flex-direction: column;
    overflow: scroll;
  }

}

  
@media screen and (min-width: 600px) {
    
    
}

@media screen and (min-width: 768px) {
    .project-navigation {
      width: 70%;
      position: static;
    }
}

/* -- Project Nav -- */
.project-navigation {
  width: 80%;
  align-items: flex-start;
  height: auto;
  font-size: .7rem;
  display: flex;
  border-radius: 7px 7px 0 0;
}

.project-navigation a {
  padding: 10px 10px ;
  text-decoration: none;
}

.project-active {
  background: rgb(185, 204, 241);
}

.project-active-left {
  border-radius: 7px 0 0 0;
}

